import React, { cloneElement, useState } from 'react';
import back from '../assets/back_5.jpg'
import iconTel from '../assets/telephone-fill.svg'
import iconSend from '../assets/send-fill.svg'
import iconArrow from '../assets/Polygone 9 (ferm).svg'
import Select, { components } from 'react-select';

const ContactUs = () => {

    const options = [
        { value: '0', label: 'Veuillez spcifier votre demande', isDisabled: true },
        { value: '1', label: 'Strawberry' },
        { value: '2', label: 'Vanilla' },
    ];

    const DropdownIndicator = (props) => {
        return (
            <components.DropdownIndicator {...props}>
                <img src={iconArrow} alt="Select arrow" />
            </components.DropdownIndicator>
        );
    };


    const customStyles = {
        dropdownIndicator: (provided, state) => ({
            ...provided,
            transform: state.selectProps.menuIsOpen ? 'rotate(180deg)' : null,
            color: '#D3414A',
            transition: 'all 300ms ease-in-out',
            '&:before': {
                display: 'none',
            },
        }),
        option: (provided, state) => ({
            ...provided,
            backgroundColor: state.isSelected ? 'rgb(200,200,200)' : provided.backgroundColor,
            color: state.isSelected ? 'white' : provided.color,
        }),
        control: (provided, state) => ({
            ...provided,
            borderRadius: 'none',
            border: 'none',
            borderBottom: 'none',
            boxShadow: 'none',
            backgroundColor: '#F6F5F6',
            fontStyle: 'sweet-sans-pro',
            padding: '17px',
            boxShadow: state.isFocused ? 'none' : provided.boxShadow,
            '&:hover': {
                borderColor: 'none',
            },
        }),
    };

    const getOptionLabel = (option) => (
        <div>
            <strong style={{ fontFamily: 'sweet-sans-pro', color: 'black', opacity: '0.4' }}>{option.label}</strong>
        </div>
    );

    const getOptionValue = (option) => option.value;


    return (
        <div >
            <div style={{ height: '95vh', backgroundImage: `url(${back})`, backgroundSize: 'cover', backgroundRepeat: 'no-repeat', backgroundPosition: '25% 75%', zIndex:'1', marginTop:'-10vh' }}>
                <div style={{ display: 'flex', justifyContent: 'end', alignItems: 'end', width: '92%', height: '95vh' }}>
                    <div style={{ backgroundColor: 'white', height: '300px', width: '600px', display: 'flex', alignItems: 'center', justifyContent: 'space-evenly', flexDirection: 'column', gap: '0px' }}>
                        <div style={{ display: 'flex', alignItems: 'center', flexDirection: 'column', height: '80%', gap: '0px' }}>
                            <h1 style={{ fontFamily: 'DM Serif Display', fontSize: '30px', fontStyle: 'normal', fontWeight: '400' }}>
                                Contactez-nous
                            </h1>
                            <div style={{ display: 'flex', flexDirection: 'column', alignItems: 'center', gap: '50px' }}>
                                <p style={{ fontFamily: 'sweet-sans-pro', fontSize: '16px', fontWeight: '400', width: '73%', textAlign: 'justify' }}>
                                    Vous avez besoin d'informations ?
                                    Vous souhaitez recevoir un devis ?
                                    Contactez-nous, nous vous rpondrons dans les plus brefs dlais
                                </p>
                            </div>
                        </div>
                    </div>
                </div>
            </div>

            <div style={{ display: 'flex', width: '100%', height: '100%', justifyContent: 'space-around', marginBottom: '10%', marginTop:'5%' }}>
                <div style={{ display: 'flex', flexDirection: 'column', width: '40%', gap: '1vh' }}>
                    <h2 style={{ marginBottom: '0%', fontFamily: 'sweet-sans-pro', fontSize: '26px', color: '#262626', textTransform: 'uppercase', fontWeight: '700', width: '50%' }}>
                        NOTRE SUPPORT
                    </h2>
                    <p style={{ fontFamily: 'sweet-sans-pro', fontSize: '18px', fontWeight: '400', width: '100%', textAlign: 'justify' }}>
                        Pour toutes demandes d'informations supplmentaires,
                        rponses  vos questions ou encore demande de devis,
                        vous pouvez nous contacter directement via le formulaire de contact prsent sur cette page ou via l'adresse mail prsente ci-dessous.<br />
                        <b>Vous voulez une rponse immdiate ?</b> Appelez-nous au numro suivant :
                    </p>
                    <div style={{ display: 'flex', alignItems: 'center', gap: '3%' }}>
                        <img src={iconTel} />
                        <label style={{ textTransform: 'capitalize', fontFamily: 'sweet-sans-pro', fontSize: '20px', color: '#262626', fontWeight: '800' }}>
                            Tlphone
                        </label>
                    </div>
                    <div style={{ display: 'flex', alignItems: 'center', gap: '3%' }}>
                        <img src={iconSend} />
                        <label style={{ textTransform: 'capitalize', fontFamily: 'sweet-sans-pro', fontSize: '20px', color: '#262626', fontWeight: '800' }}>
                            Email
                        </label>
                    </div>
                    <div>
                        <iframe src="https://www.google.com/maps/embed?pb=!1m18!1m12!1m3!1d90493.76316960942!2d-0.5860120499999999!3d44.863717799999996!2m3!1f0!2f0!3f0!3m2!1i1024!2i768!4f13.1!3m3!1m2!1s0xd5527e8f751ca81%3A0x796386037b397a89!2sBordeaux!5e0!3m2!1sfr!2sfr!4v1683636579578!5m2!1sfr!2sfr" width="650" height="550" allowfullscreen="" loading="lazy" referrerpolicy="no-referrer-when-downgrade" />
                    </div>
                </div>
                <div style={{ display: 'flex', flexDirection: 'column', width: '40%', scale: '0.95', alignSelf: 'center', height: '95%' }}>
                    <form action="" method="GET" enctype="text/plain" style={{ display: 'flex', flexDirection: 'column', gap: '5vh' }}>
                        <div style={{ display: 'flex', gap: '150px' }}>
                            <div style={{ display: 'flex', flexDirection: 'column', gap: '10px' }}>
                                <label style={{ textTransform: 'capitalize', fontFamily: 'sweet-sans-pro', fontSize: '18px', color: '#262626', fontWeight: '800' }}>
                                    Nom *
                                </label>
                                <input type='text' placeholder='Votre nom' style={{ backgroundColor: '#F6F5F6', padding: '18px', outline: 'none', border: 'none', fontFamily: 'sweet-sans-pro', fontWeight: '400', width: '120%', color: `rgba(11,28,43)` }} required />
                            </div>
                            <div style={{ display: 'flex', flexDirection: 'column', gap: '10px' }}>
                                <label style={{ textTransform: 'capitalize', fontFamily: 'sweet-sans-pro', fontSize: '18px', color: '#262626', fontWeight: '800' }}>
                                    Prnom *
                                </label>
                                <input type='text' placeholder='Votre prnom' style={{ backgroundColor: '#F6F5F6', padding: '18px', outline: 'none', border: 'none', fontFamily: 'sweet-sans-pro', fontWeight: '400', width: '120%' }} required />
                            </div>
                        </div>
                        <div style={{ display: 'flex', gap: '200px' }}>
                            <div style={{ display: 'flex', flexDirection: 'column', gap: '10px' }}>
                                <label style={{ textTransform: 'capitalize', fontFamily: 'sweet-sans-pro', fontSize: '18px', color: '#262626', fontWeight: '800' }}>
                                    Adresse mail *
                                </label>
                                <input type='text' placeholder='Votre email' style={{ backgroundColor: '#F6F5F6', padding: '18px', outline: 'none', border: 'none', fontFamily: 'sweet-sans-pro', fontWeight: '400', width: '160%' }} required />
                            </div>
                            <div style={{ display: 'flex', flexDirection: 'column', gap: '10px' }}>
                                <label style={{ textTransform: 'capitalize', fontFamily: 'sweet-sans-pro', fontSize: '18px', color: '#262626', fontWeight: '800' }}>
                                    Tlphone
                                </label>
                                <input type='number' placeholder='Votre tlphone' style={{ backgroundColor: '#F6F5F6', padding: '18px', outline: 'none', border: 'none', fontFamily: 'sweet-sans-pro', fontWeight: '400', width: '95%' }} required />
                            </div>
                        </div>
                        <div style={{ display: 'flex', flexDirection: 'column', gap: '10px', width: '80%' }}>
                            <label style={{ textTransform: 'capitalize', fontFamily: 'sweet-sans-pro', fontSize: '18px', color: '#262626', fontWeight: '800' }}>
                                Votre demande concerne *
                            </label>
                            <div>
                                <Select options={options} defaultValue={options[0]} styles={customStyles} components={{ DropdownIndicator }} getOptionLabel={getOptionLabel} getOptionValue={getOptionValue} getrequired />
                            </div>
                        </div>
                        <div style={{ display: 'flex', flexDirection: 'column', gap: '10px', width: '80%' }}>
                            <label style={{ textTransform: 'capitalize', fontFamily: 'sweet-sans-pro', fontSize: '18px', color: '#262626', fontWeight: '800' }}>
                                Prcisez votre demande
                            </label>
                            <textarea type='text' placeholder='Votre message' style={{ backgroundColor: '#F6F5F6', padding: '18px', outline: 'none', border: 'none', fontFamily: 'sweet-sans-pro', fontWeight: '400', width: '95%', height: '150px', maxWidth: '95%', maxHeight: '150px', minWidth: '95%', minHeight: '100px' }} />
                        </div>
                        <div style={{ display: 'flex', flexDirection: 'column', gap: '10px', width: '80%' }}>
                            <label style={{ textTransform: 'capitalize', fontFamily: 'sweet-sans-pro', fontSize: '18px', color: '#262626', fontWeight: '800' }}>
                                * : champ obligatoire
                            </label>
                            <div style={{ display: 'flex', alignItems: 'center', gap: '10px', width: '100%' }}>
                                <input type='checkbox' required />
                                <p style={{ fontFamily: 'sweet-sans-pro', fontSize: '17px', fontWeight: '400', width: '100%', textAlign: 'justify' }}>
                                    En soumettant ce formulaire,
                                    jaccepte que les informations saisies soient exploites dans le cadre de la demande de contact.
                                </p>
                            </div>
                        </div>
                        <div style={{ textAlign: 'center', width: '80%' }}>
                            <button type='submit' style={{ width: 'auto', color: '#ffffff', backgroundColor: '#0B1C2B', fontFamily: 'sweet-sans-pro', fontWeight: '600', fontSize: '20px', paddingLeft: '50px', paddingRight: '50px', paddingTop: '15px', paddingBottom: '15px', alignSelf: 'center', cursor: 'pointer', border:'none' }}>
                                ENVOYER
                            </button>
                        </div>
                    </form>
                </div>
            </div>
        </div>
    );
};

export default ContactUs;