import React, { cloneElement, useState } from 'react';
import back from '../assets/back.jpg'
import photo from '../assets/placeholderImage.png'
import CarousselOpiom from '../components/Carousel/CarousselOpiom';
import TablBoats from '../DataBase.js/TablBoats';
import BoatCard from '../components/Boats/BoatCard';

const HomePage = () => {

    // const [item, setItem] = useState(TablBoats);

    return (
        <div >
            <div style={{ height: '95vh', backgroundImage: `url(${back})`, backgroundSize: 'cover', backgroundRepeat: 'no-repeat', backgroundPosition: '25% 75%', zIndex:'1', marginTop:'-10vh', width:'100%' }}>
                <div style={{ display: 'flex', justifyContent: 'end', alignItems: 'end', width: '92%', height: '95vh' }}>
                    <div style={{ backgroundColor: 'white', height: '330px', width: '600px', display: 'flex', alignItems: 'center', justifyContent: 'space-evenly', flexDirection: 'column', gap: '0px' }}>
                        <div style={{ display: 'flex', alignItems: 'center', flexDirection: 'column', height: '80%', gap: '0px' }}>
                            <h1 style={{ fontFamily: 'DM Serif Display', fontSize: '30px', fontStyle: 'normal', fontWeight: '400' }}>
                                Opiom, agence vnementielle
                            </h1>
                            <div style={{ display: 'flex', flexDirection: 'column', alignItems: 'center', gap: '0px' }}>
                                <p style={{ fontFamily: 'sweet-sans-pro', fontSize: '16px', fontWeight: '500', width: '73%', textAlign: 'justify' }}>
                                    Organisez votre vnement en louant un de nos bateaux avec Opiom.
                                    Louez une salle sur la Garonne pour vos vnements tels que des sminaire,
                                    Gala, djeuner professionnel ou encore mariage, anniversaire, EVG ou EVJF.
                                </p>
                                <a href='#' className='button'>
                                    DCOUVREZ-NOUS
                                </a>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
            <div style={{ display: 'flex', justifyContent: 'space-evenly', marginTop: '10%', alignItems: 'center', marginBottom: '7%' }}>
                <div style={{ width: '40%', display: 'flex', alignItems: 'center', justifyContent: 'center', flexDirection: 'column', gap: '40px', scale: '1' }}>
                    <div style={{ width: '82%', display: 'flex', flexDirection: 'column', alignItems: 'start', gap: '20px' }}>
                        <h2 style={{ fontFamily: 'sweet-sans-pro', color: '#262626', textTransform: 'uppercase', fontSize: '26px', fontWeight: '700', margin: '0' }} >
                            POURQUOI FAIRE APPEL  OPIOM ?
                        </h2>
                        <p style={{ fontFamily: 'sweet-sans-pro', color: 'black', fontWeight: '400', width: '100%', margin: '0' }}>
                            Opiom est une agence vnementielle spcialiste de l'organisation d'vnements sur des bateaux.
                            Louez une salle en plein milieu de la Garonne et faites de vos vnements une <b>exprience unique et inoubliable !</b>
                        </p>
                        <p style={{ fontFamily: 'sweet-sans-pro', color: 'black', fontWeight: '400', width: '100%', margin: '0', }}>
                            Sminaire, gala, djeuner professionnel ou encore mariage, anniversaire,
                            EVG ou EVJF : privatiser lune de nos embarcations et marquer les esprits de vos convives !
                            <b style={{ color: 'red' }}> Privatiser un bateau</b> pour n'importe lequel de vos vnements vous permettra de vous dmarquer en changeant de cadre habituel
                            et en marquant les esprits. <b>Vos invits se souviendront longtemps de cet vnement !</b>
                        </p>
                    </div>
                    <a href='#' style={{fontSize:'15px'}} className='button'>
                        DEMANDER VOTRE DEVIS
                    </a>
                </div>
                <div style={{ width: '40%', display: 'flex', alignItems: 'center', justifyContent: 'center' }}>
                    <img src={photo} style={{ height: '400px', width: '700px', backgroundColor: '#F6F5F6' }} />
                </div>
            </div>
            <div>
                <div>
                    <h3 style={{ marginBottom: '3%', fontFamily: 'sweet-sans-pro', fontSize: '26px', color: '#262626', textTransform: 'uppercase', fontWeight: '700', width: '50%', textAlign: 'center' }}>
                        RETROUVEZ NOS DERNIERS VNEMENTS
                    </h3>
                </div>
                <div style={{ marginBottom: '7%', scale: '0.8', width: '100%' }}>
                    <CarousselOpiom />
                </div>
            </div>
            <h4 style={{ marginBottom: '3%', fontFamily: 'sweet-sans-pro', textTransform: 'uppercase', color: '#262626', fontWeight: '700', fontSize: '26px', width: '40%', textAlign: 'center' }}>
                DCOUVREZ NOS BATEAUX
            </h4>
            {TablBoats.map((item, index) => index % 2 === 1 ? <BoatCard isLeft={true} key={index} boat={item} /> : <BoatCard isLeft={false} key={index} boat={item} />)}
        </div>
    );
};

export default HomePage;